.Menu{
    background: rgba(201, 201, 201, 0.25);
box-shadow: 0 8px 22px 0 rgba(248, 96, 8, 0.37);
backdrop-filter: blur( 0px );
-webkit-backdrop-filter: blur( 0px );
border-radius: 10px;
border: 1px solid rgba( 255, 255, 255, 0.18 );
width: 95%;
padding: 10px;
transform: translateX(340%);

}
h4{
    color:rgb(255, 95, 2)
}
label{
    
    color:rgb(255, 95, 2)
    
}
input{
    padding: 12px 20px;
    border: 2px solid rgb(255, 95, 2);
    border-radius: 4px;
    background-color: rgba(255, 95, 2, 0.267);
    color: rgb(0, 0, 0);
    margin: 8px 0;
    width: 60%;
    transition: width 0.3s ease-in-out;
}
input:focus {
    width: 65%;
  }
  .btn{
   
  
  transform: translateX(180%) translateY(0%);
  border: 2px solid rgb(255, 95, 2);
  background-color: rgba(255, 95, 2, 0.267);
  color: black;
  padding: 16px 28px;
  font-size: 14px;
  cursor: pointer;
  border-radius: 8px;
  
  transition: border-radius 0.3s ease-in-out;
  }
  .success:hover{
    border-color: black;
    border-radius: 18px;
    
    color: rgb(255, 95, 2);
  }
  